/* Grid Styles when not visualizing */
.grid {
  font-size: 0;

  pointer-events: unset;
  margin-top: 20px;
  position: absolute;
  left: 50%;
  margin-right: -50%;
  overflow: hidden;
  -webkit-transform: translate(-50%, 2%);
          transform: translate(-50%, 2%);
}
/* Grid Styles when visualizing */
.visualizing-grid {
  font-size: 0;
  pointer-events: none;
  margin-top: 20px;
  position: absolute;
  left: 50%;
  margin-right: -50%;
  -webkit-transform: translate(-50%, 2%);
          transform: translate(-50%, 2%);
}

.node {
  width: var(--width);
  height: var(--height);
  border: 1px solid rgb(207, 203, 203);

  display: inline-block;
  border-collapse: collapse;
  position: relative;
}

.node-start {
  background-image: url(/myapp/static/media/triangletwo-right.c7b52e1e.svg);
  background-position: center;
  background-repeat: no-repeat;
  -webkit-animation-name: specialNodes;
          animation-name: specialNodes;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
  background-size: cover;
}

@-webkit-keyframes specialNodes {
  0% {
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
  }

  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes specialNodes {
  0% {
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
