.dropdown1 .dropdown-button {
  display: inline-block;
  border-radius: 4px;
  background-color: #38608f;
  border: none;
  color: #ffffff;
  text-align: center;
  font-size: 1.4rem;

  width: 160px;
  height: 60px;
  transition: all 0.5s;
  cursor: pointer;
}

.dropdown1 .dropdown-button span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
}

.dropdown1 .dropdown-button span:after {
  content: url(data:image/png;base64,);
  width: 10px;
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
  color: white;
}

.dropdown1 .dropdown-button:hover span {
  padding-right: 25px;
}

.dropdown1 .dropdown-button:hover span::after {
  opacity: 1;
  right: 0;
}
