/* Media Queries */
@media screen and (max-width: 980px) {
  .logo {
    width: 100%;
    text-align: start;
    margin-left: 20px;
    font-size: 1.2rem;
  }
  .menu {
    display: none;
    width: 0%;
  }
  .hamburger-menu {
    display: block;
    position: absolute;
    right: 0;

    top: 1.2rem;
    overflow: hidden;
  }
  .mobile-menu {
    position: absolute;
    z-index: 99;
  }

  .mobile-menu {
    left: 10%;

    top: 80px;
    height: 380px;
    max-height: 380px;
    width: 80%;
    background-color: rgba(0, 0, 0, 0.6);
    display: flex;

    flex-direction: column;
    align-items: center;
    transition: top 2s ease;
  }

  .mobile-menu > div {
    margin-top: -120px;
  }
}

